/* eslint-disable */
import react from 'react'
import { Input } from '@tarojs/components'
import {
  Form,
  FormItem,
  Icon,
  Switch,
  RadioGroup,
  Radio,
  CheckboxGroup,
  Checkbox,
  Slider,
  Rate,
  Stepper,
  Button,
  Dialog,
} from '@antmjs/vantui'

export default function Demo() {
  const formIt = Form.useForm()
  const [state, setState] = react.useState({})
  const [, forceUpdate] = react.useState()
  // 
  react.useEffect(() => {
    formIt.registerRequiredMessageCallback((label) => {
      return `${label}`
    })

    // setTimeout(() => {
    formIt.setErrorMessage('userName', 'xxxxx')
    // }, 1000)
  }, [])

  const handleClick = () => {
    formIt.validateFields((errorMessage, fieldValues) => {
      if (errorMessage && errorMessage.length) {
        Dialog.alert({
          message: `errorMessage: ${JSON.stringify(errorMessage)}`,
          selector: '#form-demo11',
        })
        return console.info('errorMessage', errorMessage)
      }

      Dialog.alert({
        message: `result: ${JSON.stringify(fieldValues)}`,
        selector: '#form-demo11',
      })
    })
  }

  const dynamicRule = react.useMemo(() => {
    if (formIt.getFieldValue('singleSelect') === '1') {
      return {
        rule: /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
        message: '',
      }
    }

    if (formIt.getFieldValue('singleSelect') === '2') {
      return {
        rule: /^[0-9]{6}$/,
        message: '6',
      }
    }
  }, [formIt.getFieldValue('singleSelect')])

  const dynamicPlaceHolder = react.useMemo(() => {
    if (formIt.getFieldValue('singleSelect') === '1') {
      return ''
    }

    if (formIt.getFieldValue('singleSelect') === '2') {
      return ''
    }
  }, [formIt.getFieldValue('singleSelect')])

  return (
    <>
      <Form
        initialValues={{
          userName: '',
          singleSelect: '1',
          rate: 2,
          slider: '50',
        }}
        form={formIt}
        onFinish={(errs, res) => console.info(errs, res)}
      >
        <FormItem
          label=""
          name="userName"
          required
          rules={{
            rule: /[\u4e00-\u9fa5]/,
            message: '',
          }}
          trigger="onInput"
          validateTrigger="onBlur"
          // taroinputonInpute.detail.value
          valueFormat={(e) => e.detail.value}
          renderRight={<Icon name="user-o" />}
        >
          <Input placeholder="" />
        </FormItem>

        <FormItem label="" name="opened" valueKey="checked">
          <Switch activeColor="#07c160" inactiveColor="#07c160" />
        </FormItem>

        <FormItem label="" name="singleSelect">
          {/** ,  FormItem.dependencies,  forceUpdate  getFieldValue('xx') */}
          <RadioGroup
            direction="horizontal"
            onChange={(e) => {
              formIt.setFieldsValue('cardId', '')
              forceUpdate(e)
            }}
          >
            <Radio name="1" checkedColor="#07c160">
              
            </Radio>
            <Radio name="2" checkedColor="#07c160">
              
            </Radio>
          </RadioGroup>
        </FormItem>

        <FormItem
          label=""
          name="cardId"
          required
          valueFormat={(e) => e.detail.value}
          rules={dynamicRule}
          trigger="onInput"
        >
          <Input placeholder={dynamicPlaceHolder} />
        </FormItem>

        <FormItem label="" name="muiltSelect">
          <CheckboxGroup direction="horizontal">
            <Checkbox name="1" shape="square" checkedColor="#07c160">
               1
            </Checkbox>
            <Checkbox name="2" shape="square" checkedColor="#07c160">
               2
            </Checkbox>
          </CheckboxGroup>
        </FormItem>

        <FormItem label="" name="slider">
          <Slider
            activeColor="#07c160"
            style={{ width: '200px', marginTop: '10px' }}
          />
        </FormItem>

        <FormItem label="" name="rate">
          <Rate activeColor="#07c160" />
        </FormItem>

        <FormItem label="" name="stepper">
          <Stepper />
        </FormItem>
        <Button
          type="primary"
          className="van-button-submit"
          onClick={handleClick}
          // formType="submit"
        >
          
        </Button>
      </Form>
      <Dialog id="form-demo11" />
    </>
  )
}
